import { ComponentChildren, RefObject } from 'preact';
import { useRef, useCallback, useEffect, useState, useMemo } from 'preact/hooks';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { classNames } from '../utils/UNSAFE_classNames';
import { stringLiteralArray } from '../utils/UNSAFE_arrayUtils';
import { mergeProps } from '../utils/UNSAFE_mergeProps';
import { Keys, isKeyDefined } from '../utils/UNSAFE_keys';
import {
  CurrentKeyDetail,
  DataState,
  ListItemContext,
  Metadata,
  Range,
  SelectionDetail,
  SelectionMode
} from '../UNSAFE_Collection';
import {
  VirtualizedCollection,
  ViewportConfig,
  PLACEHOLDER_STYLE_CLASS
} from '../UNSAFE_VirtualizedCollection';
import { ListViewItem, ITEM_STYLE_CLASS } from './ListViewItem';
import { SelectionRangeDetail, useSelection } from '../hooks/PRIVATE_useSelection';
import { useCurrentKey } from '../hooks/PRIVATE_useCurrentKey';
import { useCollectionFocusRing } from '../hooks/PRIVATE_useCollectionFocusRing';
import { SkeletonContainer } from './SkeletonContainer';
import { Skeleton } from '../UNSAFE_Skeleton';
import { Flex } from '../UNSAFE_Flex';
/**
 * Type for gridlines
 */
export const gridlinesValues = stringLiteralArray(['visible', 'hidden']);
export type Gridlines = typeof gridlinesValues[number];

/**
 * Props for the ListView Component
 */
export type Props<K, D> = {
  /**
   * A DataState object that provide information including data and metadata to this ListView.
   * If the value is null, then ListView will show loading indicator until a DataState is set.
   */
  data: DataState<K, D> | null;

  /**
   * A function to render each item
   */
  children: (context: ListItemContext<K, D>) => ComponentChildren;

  /**
   * Callback function to handle when viewport has changed, including the case
   * where user scrolls to the end of the component and there are more items to load.
   * The function should sets a new DataState on the component for the specified range.
   */
  onLoadRange: (range: Range) => void;

  /**
   * A text that provides a summary of this ListView for the purpose of accessibility.
   * This is required in order to make ListView accessible.
   */
  accessibleSummary: string;

  /**
   * The key of the item that currently have keyboard focus. Ignored if the current item is not
   * currently in the viewport.
   */
  currentKey?: K;

  /**
   * Specifies whether the horizontal grid lines should be visible.  By default gridlines
   * are hidden.
   */
  gridlines?: Gridlines;

  /**
   * The keys of the current selected items in the ListView.
   */
  selectedKeys?: Keys<K>;

  /**
   * The type of selection behavior that is enabled on the ListView. This property controls the number
   * of selections that can be made via selection gestures at any given time.
   */
  selectionMode?: SelectionMode;

  /**
   * Callback function to handle when current focused item has changed.  The function should
   * update the currentKey prop with a new current key.
   */
  onCurrentKeyChange?: (detail: CurrentKeyDetail<K>) => void;

  /**
   * Callback function to handle when selection has changed.  The function should update
   * the selectedKeys prop with a new set of selected keys.
   */
  onSelectionChange?: (detail: SelectionDetail<K>) => void;

  /**
   * Viewport configuration which the application can specify the element to determine what range
   * of data to render.
   */
  viewportConfig?: ViewportConfig;
};

const styles = {
  base: cssProps`${{
    height: '100%',
    userSelect: 'none',
    overflowY: 'inherit',
    '&:focus': {
      outlineStyle: 'none'
    }
  }}`
};

const sparkleStyles = {
  base: cssProps`${{
    position: 'absolute',
    top: 0,
    width: '4px',
    zIndex: 1,
    display: 'block',
    backgroundImage: 'var(--oj-c-PRIVATE-DO-NOT-USE-collection-sparkle-img)',
    backgroundRepeat: 'no-repeat',
    backgroundSize: '100% 100%'
  }}`,
  container: cssProps`${{
    position: 'relative'
  }}`
};

const getViewportConfig = (rootRef: RefObject<HTMLElement>, config?: ViewportConfig) => {
  return (
    config ?? {
      scroller: () => {
        return rootRef.current;
      }
    }
  );
};

const emptyKeys = { all: false, keys: new Set() };

const handleSelectionRange = <K, D>(
  detail: SelectionRangeDetail<K>,
  dataState: DataState<K, D>
) => {
  const keys = dataState.data.map((value: { data: D; metadata: Metadata<K> }) => {
    return value.metadata.key;
  });
  const startIndex = keys.indexOf(detail.value.start);
  const endIndex = keys.indexOf(detail.value.end);
  const minIndex = Math.min(startIndex, endIndex);
  const maxIndex = Math.max(startIndex, endIndex);
  if (minIndex === -1) {
    // return a range to fetch, maxIndex should not be -1
    return { offset: 0, count: dataState.offset + keys.length };
  } else {
    // range of keys are in the current viewport, return them
    return keys.slice(minIndex, maxIndex + 1);
  }
};

/**
 * A sparkle component for smart suggestion indicator in ListView
 * @param sparkleHeight the height of sparkle
 */
export function Sparkle({ sparkleHeight }: { sparkleHeight: number }) {
  if (sparkleHeight <= 0) {
    return null;
  }

  const height = sparkleHeight + 'px';
  const sparkleClasses = classNames([sparkleStyles.base]);
  const containerClasses = classNames([sparkleStyles.container]);

  return (
    <div key="sparkle" class={containerClasses}>
      <div class={sparkleClasses} style={{ height }}></div>
    </div>
  );
}

/**
 * Component that renders items as a flat list.
 * In order to maximize performance, only items that are visible in the viewport are rendered.
 */
export function ListView<K extends string | number, D>({
  accessibleSummary,
  data,
  children,
  currentKey,
  gridlines = 'hidden',
  selectedKeys = emptyKeys as Keys<K>,
  selectionMode = 'none',
  onCurrentKeyChange,
  onLoadRange,
  onSelectionChange,
  viewportConfig
}: Props<K, D>) {
  const rootRef = useRef<HTMLDivElement>(null);

  // tracking the anchor key which is used for shift+click selection
  const anchorKey = useRef<K | undefined>();

  // tracking
  const pendingSelection = useRef<SelectionRangeDetail<K>>();

  // tracking sparkle height
  const [sparkleHeight, setSparkleHeight] = useState<number>(0);

  const showGridline = (index: number) => {
    // gridlines are positioned at the top so never show it for the first item
    return gridlines === 'visible' && index > 0;
  };

  const ariaMultiSelectable = selectionMode === 'none' ? undefined : selectionMode === 'multiple';
  const ariaRowCount =
    data === null ? undefined : data.sizePrecision === 'exact' ? data.totalSize : -1;

  const classes = classNames([styles.base]);

  const { currentKeyProps } = useCurrentKey(
    keyExtractor,
    getPrevNextKey(rootRef.current, currentKey, true),
    getPrevNextKey(rootRef.current, currentKey, false),
    currentKey,
    onCurrentKeyChange
  );

  const handleInitialFocus = useCallback(() => {
    if (rootRef.current && onCurrentKeyChange) {
      const firstKey = getFirstVisibleKey(rootRef.current) as K;
      if (isKeyDefined(firstKey)) {
        onCurrentKeyChange({ value: firstKey });
      }
    }
  }, [currentKey, onCurrentKeyChange]);

  const focusHandler =
    !isKeyDefined(currentKey) && onCurrentKeyChange ? { onFocus: handleInitialFocus } : {};

  const [showFocusRing, focusRingProps] = useCollectionFocusRing(
    (elem: Element | null) => {
      return !!rootRef.current?.contains(elem);
    },
    ['ArrowUp', 'ArrowDown']
  );

  useEffect(() => {
    if (currentKey != null && rootRef.current) {
      const elem = findElementByKey(rootRef.current, currentKey);
      if (elem) {
        const scroller = viewportConfig?.scroller();
        // make sure item is visible
        scrollToVisible(elem, scroller != null ? scroller : rootRef.current);
        const cell = elem.querySelector('[role=option]');
        // update aria-activedescendant for screenreader
        if (cell) {
          rootRef.current.setAttribute('aria-activedescendant', cell.id);
        }
      }
    }
  }, [currentKey]);

  const handleSelectionChange = useCallback(
    (detail: SelectionDetail<K>) => {
      if (onSelectionChange) {
        if (detail.value.all === false && detail.value.keys.size > 0) {
          // the last key selected by a user gesture is the anchor key
          anchorKey.current = Array.from(detail.value.keys).pop();
        }
        onSelectionChange(detail);
      }
    },
    [anchorKey, onSelectionChange]
  );

  const handleSelectionRangeChange = useCallback(
    (detail: SelectionRangeDetail<K>) => {
      if (data && onSelectionChange) {
        const value = handleSelectionRange(detail, data);
        if (Array.isArray(value)) {
          onSelectionChange({ value: { all: false, keys: new Set(value) } });
        } else {
          pendingSelection.current = detail;
          onLoadRange(value);
        }
      }
    },
    [data, onSelectionChange, pendingSelection, onLoadRange]
  );

  const { selectionProps } = useSelection(
    (event: Event) => {
      if (event.target === rootRef.current) {
        return currentKey === undefined ? null : currentKey;
      }
      return keyExtractor(event) as K;
    },
    selectedKeys,
    selectionMode,
    false,
    'replace',
    handleSelectionChange,
    anchorKey.current,
    currentKey,
    (currentKey: K | undefined, isPrev: boolean) =>
      getPrevNextKey(rootRef.current, currentKey, isPrev),
    (key: K) => {
      if (rootRef.current) {
        const elem = findElementByKey(rootRef.current, key);
        if (elem) {
          const scroller = viewportConfig?.scroller();
          scrollToVisible(elem, scroller != null ? scroller : rootRef.current);
        }
      }
    },
    handleSelectionRangeChange
  );

  if (data && pendingSelection.current && onSelectionChange) {
    const keys = handleSelectionRange(pendingSelection.current, data);
    if (Array.isArray(keys)) {
      onSelectionChange({ value: { all: false, keys: new Set(keys) } });
      pendingSelection.current = undefined;
    }
  }

  const loadMoreIndicator = (
    <SkeletonContainer minimumCount={3}>
      {() => {
        return (
          <Flex height="12x" align="center">
            <Skeleton height="4x" />
          </Flex>
        );
      }}
    </SkeletonContainer>
  );

  const suggestions = useMemo(() => findSuggestions(data), [data?.data]);

  // TODO: check suggestions before Sparkle would cause a scrolling issue
  const sparkleIndicator = <Sparkle sparkleHeight={sparkleHeight}></Sparkle>;

  useEffect(() => {
    // update sparkleHeight only when we have suggestions
    // avoid unnecessary iterating through elements
    if (suggestions) {
      const placeholder = rootRef.current?.querySelector(
        '.' + PLACEHOLDER_STYLE_CLASS
      ) as HTMLElement;
      let height = placeholder.offsetHeight;
      const suggestionItems = rootRef.current?.querySelectorAll('[data-oj-suggestion]');
      suggestionItems?.forEach((item) => (height += (item as HTMLElement).offsetHeight));
      setSparkleHeight(height);
    }
  }, [suggestions]);

  // if data is not specified, listview should show loading indicator
  // todo: replace placeholder with actual SkeletonContainer component (Ash is working on)
  return (
    <div
      {...mergeProps(currentKeyProps, selectionProps, focusRingProps, focusHandler)}
      role="listbox"
      aria-rowcount={ariaRowCount}
      aria-colcount={1}
      ref={rootRef}
      class={classes}
      tabIndex={0}
      aria-label={accessibleSummary}
      aria-multiselectable={ariaMultiSelectable}>
      {data == null ? (
        <SkeletonContainer>
          {() => {
            return (
              <Flex height="12x" align="center">
                <Skeleton height="4x" />
              </Flex>
            );
          }}
        </SkeletonContainer>
      ) : (
        <VirtualizedCollection
          data={data}
          onLoadRange={onLoadRange}
          itemSelector={'.' + ITEM_STYLE_CLASS}
          viewportConfig={getViewportConfig(rootRef, viewportConfig)}
          loadMoreIndicator={loadMoreIndicator}
          suggestions={sparkleIndicator}>
          {(context: ListItemContext<K, D>) => {
            const suggestion = suggestions?.get(context.metadata.key);
            return (
              <ListViewItem
                itemIndex={context.index}
                itemKey={context.metadata.key}
                currentKey={currentKey}
                isFocusRingVisible={showFocusRing}
                isGridlineVisible={showGridline(context.index)}
                selectedKeys={selectedKeys}
                selectionMode={selectionMode}
                onSelectionChange={handleSelectionChange}
                {...(suggestion && { suggestion })}>
                {children(context)}
              </ListViewItem>
            );
          }}
        </VirtualizedCollection>
      )}
    </div>
  );
}

/**
 * A helper function that finds the data with suggestions, and
 * returns the corresponding keys
 * @param dataState
 */
const findSuggestions = <K, D>(dataState?: DataState<K, D> | null) => {
  const data = dataState?.data;
  const count = getSuggestionsCount(data);
  if (count === 0) {
    return null;
  }

  const suggestionsData = data!.slice(0, count);
  return suggestionsData.reduce((suggestions, value, index) => {
    const key = value.metadata.key;

    if (index === count - 1) {
      suggestions.set(key, 'end');
    } else {
      suggestions.set(key, true);
    }

    return suggestions;
  }, new Map<K, 'end' | true>());
};

/**
 * A helper function that finds the number of suggestions
 * @param data
 */
const getSuggestionsCount = <K, D>(data?: DataState<K, D>['data']) => {
  if (!data) {
    return 0;
  }

  const index = data.findIndex((value) => !value.metadata.suggestion);
  return index === -1 ? 0 : index;
};

/**
 * Returns the key from the item element
 */
const getKey = (item: HTMLElement) => {
  return item.dataset['ojKeyType'] === 'number'
    ? Number(item.dataset['ojKey'])
    : item.dataset['ojKey'];
};

/**
 * A helper function to find the element with the specified key.
 * Returns null if the element with key is not found.
 */
const findElementByKey = <K extends string | number>(root: HTMLElement, key: K) => {
  if (root) {
    const items = root.querySelectorAll('.' + ITEM_STYLE_CLASS);
    for (let i = 0; i < items.length; i++) {
      const thisKey = getKey(items[i] as HTMLElement);
      if (key == (thisKey as K)) {
        return items[i];
      }
    }
  }
  return null;
};

/**
 * A helper function that return the key of the first available item.
 */
const getFirstVisibleKey = (root: HTMLElement) => {
  if (root) {
    const firstItem = root.querySelector('.' + ITEM_STYLE_CLASS);
    if (firstItem) {
      const key = getKey(firstItem as HTMLElement);
      return key;
    }
  }
  return null;
};

/**
 * A helper function that returns the key of the previous or the next item
 * given the specified current key
 */
const getPrevNextKey = <K extends string | number>(
  root: HTMLElement | null,
  currentKey: K | undefined,
  isPrev: boolean
) => {
  return (): K | null => {
    if (isKeyDefined(currentKey) && root) {
      const currItem = findElementByKey(root, currentKey as K);
      if (currItem) {
        const nextItem = isPrev ? currItem.previousElementSibling : currItem.nextElementSibling;
        if (nextItem) {
          return getKey(nextItem as HTMLElement) as K;
        }
      }
      return getFirstVisibleKey(root) as K;
    }
    return null;
  };
};

/**
 * A helper function to make sure specified elem is visible in the specified container
 */
const scrollToVisible = (elem: Element, scroller: Element) => {
  if (elem && scroller) {
    const scrollerBounds = scroller.getBoundingClientRect();
    const elemBounds = elem.getBoundingClientRect();
    if (elemBounds.bottom > scrollerBounds.bottom) {
      scroller.scrollTop = scroller.scrollTop + (elemBounds.bottom - scrollerBounds.bottom);
    } else if (elemBounds.top < scrollerBounds.top) {
      scroller.scrollTop = scroller.scrollTop - (scrollerBounds.top - elemBounds.top);
    }
  }
};

/**
 * A helper function to get the key from an event
 */
const keyExtractor = <K extends string | number>(event: Event): K | null => {
  const elem = event.target as HTMLElement;
  const item = elem.closest('.' + ITEM_STYLE_CLASS);
  if (item) {
    const key = getKey(item as HTMLElement) as K;
    return key === undefined ? null : key;
  }
  return null;
};
