import { ComponentChildren } from 'preact';
import { useRef } from 'preact/hooks';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { classNames } from '../utils/UNSAFE_classNames';
import { Keys, containsKey } from '../utils/UNSAFE_keys';
import { SelectionDetail, SelectionMode } from '../UNSAFE_Collection';
import { Selector } from '../UNSAFE_Selector';
import { useId } from '../hooks/UNSAFE_useId';

// default style class for items in ListView
export const ITEM_STYLE_CLASS = 'oj-listview-item';

const styles = {
  base: cssProps`${{
    display: 'flex',
    alignItems: 'center',
    minHeight: 'var(--oj-collection-list-row-height)',
    boxSizing: 'border-box',
    backgroundImage:
      'linear-gradient(var(--oj-c-PRIVATE-DO-NOT-USE-core-divider-color),var(--oj-c-PRIVATE-DO-NOT-USE-core-divider-color))',
    backgroundSize: 0,
    backgroundPosition: '0 0',
    backgroundRepeat: 'no-repeat',
    borderTop: '1px solid transparent'
  }}`,
  gridlines: cssProps`${{
    backgroundSize: '100% 1px'
  }}`,
  selectable: cssProps`${{
    '&:hover': {
      backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-bg-color-hover);'
    }
  }}`,
  selected: cssProps`${{
    backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-bg-color-selected)'
  }}`,
  selectedSingle: cssProps`${{
    borderTop: '1px solid var(--oj-c-PRIVATE-DO-NOT-USE-core-border-color-selected)',
    boxShadow: 'inset 0 -1px var(--oj-c-PRIVATE-DO-NOT-USE-core-border-color-selected)'
  }}`,
  // TODO: revisit later to prevent double lines when implementing gridlines feature
  suggestionEnd: cssProps`${{
    boxShadow: 'inset 0 -1px var(--oj-c-PRIVATE-DO-NOT-USE-core-divider-color)'
  }}`
};

const cellStyles = {
  base: cssProps`${{
    flexGrow: '1',
    '&:focus': {
      outlineStyle: 'none'
    }
  }}`,
  checkboxContainer: cssProps`${{
    display: 'flex'
  }}`,
  focused: cssProps`${{
    outlineStyle: 'dotted',
    outlineWidth: '1px',
    outlineColor: 'var(---oj-c-PRIVATE-DO-NOT-USE-core-focus-border-color)',
    outlineOffset: '-1px'
  }}`
};

/**
 * Props for the ListViewItem Component
 */
export type Props<K> = {
  children: ComponentChildren;
  itemKey: K;
  itemIndex: number;
  currentKey?: K;
  selectedKeys: Keys<K>;
  selectionMode: SelectionMode;
  isFocusRingVisible: boolean;
  isGridlineVisible: boolean;
  suggestion?: 'end' | true;
  onSelectionChange?: (detail: SelectionDetail<K>) => void;
};

/**
 * Helper method to determine whether item is selectable
 * @returns true if item is selectable (show hover effect), false otherwise
 */
const isSelectable = (selectionMode: SelectionMode, isItemSelected: boolean) => {
  return selectionMode !== 'none' && (selectionMode === 'multiple' || !isItemSelected);
};

/**
 * The internal component used to render a single item in ListView.
 */
export function ListViewItem<K extends string | number>({
  children,
  itemKey,
  itemIndex,
  currentKey,
  isFocusRingVisible,
  isGridlineVisible,
  suggestion,
  selectedKeys,
  selectionMode,
  onSelectionChange
}: Props<K>) {
  const rootRef = useRef<HTMLDivElement>(null);
  const isFocused = currentKey == itemKey;
  const isSelected = containsKey(selectedKeys, itemKey);
  const classes = classNames([
    styles.base,
    ITEM_STYLE_CLASS,
    isGridlineVisible && styles.gridlines,
    isSelectable(selectionMode, isSelected) && !isSelected && styles.selectable,
    isSelected && styles.selected,
    isSelected && selectionMode === 'single' && styles.selectedSingle,
    suggestion === 'end' && !(isSelected && selectionMode === 'single') && styles.suggestionEnd
  ]);

  const cellClasses = classNames([
    cellStyles.base,
    selectionMode === 'multiple' && cellStyles.checkboxContainer,
    isFocused && isFocusRingVisible && cellStyles.focused
  ]);

  // todo: use translated text for selector
  // note cannot put focus on gridcell div since JAWS will not read the aria-rowindex
  // correctly, so unfortunately needed another div
  return (
    <div
      role="presentation"
      aria-rowindex={itemIndex + 1}
      key={itemKey}
      data-oj-key={itemKey}
      data-oj-suggestion={!!suggestion}
      class={classes}
      ref={rootRef}
      {...(typeof itemKey === 'number' && { 'data-oj-key-type': 'number' })}>
      <div
        id={useId()}
        class={cellClasses}
        role="option"
        aria-posinset={itemIndex + 1}
        aria-setsize={-1}
        aria-colindex={1}
        aria-selected={selectionMode !== 'none' ? isSelected : undefined}>
        {selectionMode === 'multiple' && (
          <Selector
            rowKey={itemKey}
            selectedKeys={selectedKeys}
            onChange={onSelectionChange}></Selector>
        )}
        {children}
      </div>
    </div>
  );
}
